	module.exports = {"data":{"markdownRemark":{"html":"<h3>Hacker Feedback Dashboard</h3>\n<p>We introduce the new Hacker Feedback Dashboard where private programs can see the total feedback their program has received from hackers along with the reasons they’ve declined to participate in their program. The feedback can be viewed at <b>Dashboard > Feedback</b>. Learn more about the <a href=\"/programs/hacker-feedback-dashboard.html\">feedback dashboard</a>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-430ef.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 45.0314465408805%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"may_2018\"\n        title=\"\"\n        src=\"/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-e7ffa.png\"\n        srcset=\"/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-49813.png 125w,\n/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-dc8a5.png 250w,\n/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-e7ffa.png 500w,\n/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-dda24.png 750w,\n/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-c5f18.png 1000w,\n/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-22484.png 1500w,\n/static/may_2018-fdd025fa8ad295e6d5366dd1ae7c84ef-430ef.png 1590w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Triggers</h3>\n<p>We've revamped our <a href=\"/programs/triggers.html\">triggers</a> functionality so that you can: </p>\n<ul>\n<li>Preview matches for a new trigger</li>\n<li>Add <i>And/Or</i> conditionals to make the trigger more flexible</li>\n<li>Edit or build off of default triggers</li>\n</ul>\n<p>We've also updated the design so that you'll have a better user experience.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-8a081.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 83.10708898944193%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"may_2018_triggers\"\n        title=\"\"\n        src=\"/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-e7ffa.png\"\n        srcset=\"/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-49813.png 125w,\n/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-dc8a5.png 250w,\n/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-e7ffa.png 500w,\n/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-dda24.png 750w,\n/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-c5f18.png 1000w,\n/static/may_2018_triggers-6fc65f79655000c23a3901c06b1d2282-8a081.png 1326w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Response Targets</h3>\n<p>We’ve deprecated the threatening term, <i>Response SLA</i> and replaced it with the more friendly terms, <i>Response Targets</i> and <i>Response Standards</i>. <a href=\"/programs/response-targets.html\">Learn more about these new terms</a>. </p>\n<p>We’ve deprecated the <i>SLA Violations</i> inbox view and changed the name to <b>Missed targets</b>. The inbox filters are also now <b>Missed response targets</b> and <b>Missed response standards</b> instead of <i>SLA violation reports</i> and <i>SLA Fail reports</i>. </p>\n<p>We introduce 4 new inbox labels for reports that don’t meet response standards or targets. The labels are: Response, Triage, Bounty, and Resolve. These labels replace the previous <i>SLA Fail</i> and <i>SLA Miss</i> labels.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-30e60.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 401px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 46.88279301745636%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"may_2018_label\"\n        title=\"\"\n        src=\"/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-92414.png\"\n        srcset=\"/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-fb5ce.png 125w,\n/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-03076.png 250w,\n/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-92414.png 500w,\n/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-401d3.png 750w,\n/static/may_2018_responselabel-f8c9ac301194802a761323bc138d1623-30e60.png 802w\"\n        sizes=\"(max-width: 401px) 100vw, 401px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>The fields on the Reponse Target performance section of the Program Health dashboard have changed to <b>On target</b>, <b>Missed target</b>, and <b>Missed standard</b>. The missed target line is also taken off of the Average Time to Resolution graph on the dashboard.</p>\n<h3>Response Efficiency Indicators</h3>\n<p>We’ve modified response efficiency indicators so that:</p>\n<ul>\n<li>They now let you know the program’s percentage of reports that meet response standards instead of the number of reports that are failing or missing SLAs. </li>\n<li>The indicator and metrics are visible even when a member of the program is signed out. </li>\n<li>The orange response efficiency indicator is now changed to yellow.</li>\n<li>The indicator now occurs at the bottom of the metrics chart instead of at the top.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2018_efficiencyindicator-21d25dd81de420a69b2fc30fef0f082c-1cbcb.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 221px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 142.98642533936652%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"may_2018_indicator\"\n        title=\"\"\n        src=\"/static/may_2018_efficiencyindicator-21d25dd81de420a69b2fc30fef0f082c-1cbcb.png\"\n        srcset=\"/static/may_2018_efficiencyindicator-21d25dd81de420a69b2fc30fef0f082c-c32c2.png 125w,\n/static/may_2018_efficiencyindicator-21d25dd81de420a69b2fc30fef0f082c-27112.png 250w,\n/static/may_2018_efficiencyindicator-21d25dd81de420a69b2fc30fef0f082c-1cbcb.png 442w\"\n        sizes=\"(max-width: 221px) 100vw, 221px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </li>\n</ul>\n<h3>Time to Resolution by Severity</h3>\n<p>We now enable you to set your Time to Resolution response standards by severity. Learn more <a href=\"/programs/setting-response-targets.html\">here</a>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-53f60.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 41.446208112874785%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"may_2018_severity\"\n        title=\"\"\n        src=\"/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-e7ffa.png\"\n        srcset=\"/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-49813.png 125w,\n/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-dc8a5.png 250w,\n/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-e7ffa.png 500w,\n/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-dda24.png 750w,\n/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-c5f18.png 1000w,\n/static/may_2018_resbyseverity-ab2d30104c2080f824a0fdd707bfac88-53f60.png 1134w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Invitations Toggle</h3>\n<p>Programs no longer have the ability to toggle invitations on or off with the On/Off button. The equivalent action to turn invitations off is to set the report volume to 0 if they no longer wish to engage with new hackers. To turn invitations on, just increase the report volume to be greater than 0. </p>\n<h3>Policy and Scope</h3>\n<p>Policy and Scope now have their own separate sections under <b>Settings > Program</b>. </p>\n<h3>Bug Fixes</h3>\n<ul>\n<li>The Managed label no longer shows up on the directory for programs with expired triage subscriptions.</li>\n<li>The response standard percentage now displays when the display option setting is enabled. There were some incidences where it didn’t show in the past. </li>\n<li>When a large user profile photo is uploaded, an error message is now given to the user to notify them that the upload has failed. </li>\n<li>Social sharing icons on public programs are now aligned and work properly without any weird spacing issues between the icons. </li>\n<li>Hackers no longer receive automatic invitations for programs they’ve left. </li>\n</ul>","frontmatter":{"path":"/changelog/2018/may","title":"May 2018"}}},"pathContext":{}}

